$midnight: #2c3e50;
$clouds: #f8fbfc;
$white: #ffffff;
$verdecito: #d4f8e8;
$verde: #1eb2a6;

// General

.row input {
  position: absolute;
  opacity: 0;
  z-index: -1;
}
// Layout
.row {
  display: flex;
  justify-content: center;
  padding: 20px;
}

.row hr {
  border: none;
  margin: 2px;
}

/* Accordion styles */
.tabs {
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 4px 4px -2px rgba(0, 0, 0, 0.3);
  max-width: 1160px;
}
.tab {
  color: $midnight;
  overflow: hidden;
  &-label {
    display: flex;
    justify-content: space-between;
    padding: 1em;
    background: $white;
    font-weight: bold;
    font-size: 1.6vmax;
    cursor: pointer;
    /* Icon */
    &:hover {
      background: darken($verdecito, 10%);
    }
    &::after {
      content: "\276F";
      width: 1em;
      height: 1em;
      text-align: center;
      transition: all 0.35s;
    }
  }
  &-content {
    max-height: 0;
    padding: 0 1em;
    color: $midnight;
    background: white;
    transition: all 0.35s;
  }
  &-close {
    display: flex;
    justify-content: flex-end;
    padding: 1em;
    font-size: 0.75em;
    background: $midnight;
    cursor: pointer;
    &:hover {
      background: darken($midnight, 10%);
    }
  }
}

.tab a {
  text-decoration: none;
  color: inherit;
}

.row h2 {
  font-size: 3vmax;
}

.tab-content {
  font-size: 1.6vmax;
}
// :checked
.row input:checked {
  + .tab-label {
    background: darken($verdecito, 10%);
    &::after {
      transform: rotate(90deg);
    }
  }
  ~ .tab-content {
    max-height: 100vh;
    padding: 1em;
  }
}

